import React, {PureComponent} from 'react';
import Head from 'next/head';
import Button from 'antd/lib/button';
import Carousel from 'antd/lib/carousel';
import Icon from 'antd/lib/icon';
import '../style.css';




export default class Home extends PureComponent {
	

	renderHeader = () => {
		return (
				<div className="home-header">
					<div className="home-logo">
						logo
					</div>
					<div className="home-contact">
						<span><Icon type="mail" />youlikeisthebest@163.com</span>
						<span><Icon type="mobile" /></span>
					</div>
					<style>{`
						.home-header {
							display: flex;
							justify-content: space-between;
							align-items: center;
						}
					`}</style>
				</div>
			)
	}
	renderCarousel = () => {
		return (
				<div>
					<Carousel autoplay>
					    <div className="carousel-img"><img src="/static/img/carousel-1.jpeg" /></div>
					    <div className="carousel-img"><img src="/static/img/carousel-2.jpeg" /></div>
					    <div className="carousel-img"><img src="/static/img/carousel-3.jpeg" /></div>
			    	</Carousel>
			    	<style>{`
			    		.carousel-img {
							height: 400px;
						}
						.carousel-img img {
							height: 400px;
						}
		    		`}</style>
				</div>
				
			)

	}

	render() {
		return (
			<div>
				<Head>
					<title>home</title>
				    <meta name="viewport" content="initial-scale=1.0, width=device-width" />
				    <link rel='stylesheet' href='https://cdnjs.cloudflare.com/ajax/libs/antd/3.6.3/antd.css'/>
				</Head>
				<div className="framework">
					{this.renderHeader()}
				
					{this.renderCarousel()}
					<p>港机销售</p>
					<Button type="primary">Button</Button>

				</div>
				<style jsx>{`
					.home-header {
						display: flex;
						justify-content: space-between;
						align-items: center;
					}
					
				`}</style>
			</div>
		)
	}
}	



